@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
:root {
  --bg: #1f1f38;
  --bg-variant: #2c2c6c;
  --primary: #4db5ff;
  --primary-variant: rgba(77, 181, 255, 0.4);
  --white: #fff;
  --light: rgba(255, 255, 255, 0.6);
  --container-width-lg: 75%;
  --container-width-md: 80%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
}
html {
  scroll-behavior: smooth;
}
::-webkit-scrollbar {
  display: none;
}
body {
  font-family: 'Poppins', sans-serif;
  background: var(--bg);
  color: var(--white);
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
}
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}
h1 {
  font-size: 2.5rem;
}
.section {
  margin-top: 8rem;
}
.section > h2,
.section > h5 {
  text-align: center;
  color: var(--light);
}
.section > h2 {
  color: var(--primary);
  margin-bottom: 3rem;
}
.text-light {
  color: var(--light);
}
a {
  color: var(--primary);
  transition: var(--transition);
}
a:hover {
  color: var(--white);
}
.btn {
  width: max-content;
  display: inline-block;
  color: var(--primary);
  padding: .75rem 2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--primary);
  transition: var(--transition);
}
.btn:hover {
  background: var(--white);
  color: var(--bg);
  border-color: transparent;
}
.btn-primary {
  background: var(--primary);
  color: var(--bg);
}
img {
  display: block;
  width: 100%;
  object-fit: contain;
}

@media (min-width: 768px) and (max-width: 1199px) {
  .container {
    width: var(--container-width-md);
  }
  .section {
    margin-top: 6rem;
  }
}

@media (max-width: 767px) {
  .container {
    width: var(--container-width-sm);
  }
  .section > h2 {
    margin-bottom: 2rem;
  }
}